import React, { Component } from 'react'
import NewsItem from './NewsItem'

export class news extends Component {
    constructor(){
        super();
        console.log("hELLO I aM A CONSTRUCTOR FROM NEWS COMPONENT ")
        this.state = {
            articles:[],
            loading:false,
            page:1
       }
    }

    async componentDidMount(){
        let url ="https://newsapi.org/v2/top-headlines?country=in&category=business&apiKey=9561da83005649bb9d198deb8dd116c0&page=1&pageSize=20";
        let data = await fetch(url);
        let parseddata = await data.json();
        console.log(parseddata);
        this.setState({articles:parseddata.articles,totalresults:parseddata.totalresults})

    }
    handleNextclick= async()=>{
console.log("NEXT");
if (this.state.page+1>Math.ceil(this.state.totalresults/20)) {
    
} else {
    let url =`https://newsapi.org/v2/top-headlines?country=in&category=business&apiKey=9561da83005649bb9d198deb8dd116c0&page=${this.state.page+1}&pageSize=20`;
let data = await fetch(url);
let parseddata = await data.json();
console.log(parseddata);

this.setState({
    page:this.page+1,
    articles:parseddata.articles
})
}

    }
    handlePreviousclick= async()=>{
        console.log("Previous");
        let url =`https://newsapi.org/v2/top-headlines?country=in&apiKey=9561da83005649bb9d198deb8dd116c0&page=${this.state.page-1}&pageSize=20`;
        let data = await fetch(url);
        let parseddata = await data.json();
        console.log(parseddata);
        
        this.setState({
            page:this.page-1,
            articles:parseddata.articles
        })
    }
    render() {
        return (
            <div className="container my-3">
                
        <h2>NewsAp- Top Headlines </h2>
        <div className="row">
            {this.state.articles.map((element)=>{
                    return <div className="col-md-4" key={element.url}>
                    <NewsItem title={element.title} description={element.description} urltoima={element.urlToImage} newsurl={element.url} />
           
                    </div>
            })}

         
        
         
        </div>
        <div className="d-flex justify-content-between">
            <button disabled={this.state.page<=1} type="button" className="btn btn-dark" onClick={this.handlePreviousclick}>&lArr; Previous </button>
               <button disabled={this.state.page+1>Math.ceil(this.state.totalresults/20)} type="button" className="btn btn-dark" onClick={this.handleNextclick}>Next &rArr; </button></div>
               
                   </div>   
               

            
        )
    }
}

export default news
